.hh{
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(12px);
    height: 100%;
    max-height: 77px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
}

.nnav{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 100%;
}

.nav-logo{
    font-family: 'Archivo Black';
    font-weight: 400;
    font-size: 35px;
    line-height: 38px;
}

.select-language{
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 3px;
    width: 100%;
    height: auto;
    max-width: 58px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8px;
    gap:10px
}

.nav-section-2{
    width: auto;
    display: flex;
    gap: 21px;
}