.coffee-background{
    background: url(../images/coffee-back.jpg) no-repeat;
    width: 100%;
    height: 100%;
    max-height: 787px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-block-start: 78px;
}

.bean-background{
    width: 100%;
    height: 100%;
    max-height: 768px;
    display: flex;
    flex-direction: column;
    padding-block: 77px;
    box-sizing: border-box;
}

.bean-p{
    width: 100%;
    max-width: 1163px;
    font-family: 'Apercu';
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: 36px;
    text-align: center;

    color: #000000;
}

.beans{
    position: absolute;
    top: 0;
    z-index: -1;
    height: 100%;
    max-height: 428px;
}

.bean-p-div{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.tape-strip{
    position: absolute;
    left:200px;
    margin-top: -120px;
    z-index: 1;
}

.coffee-story{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-block: 50px;
}

.hr-coffee{
    width: 302px;
    height: 4px;
    background: #95D6F1;
    border-style: none;
}